export const FETCH_GRAPH = 'FETCH_GRAPH';
export const FETCH_GRAPH_COMPLETE = 'FETCH_GRAPH_COMPLETE';
export const FETCH_GRAPH_FAILED = 'FETCH_GRAPH_FAILED';

export function fetchGraph(requirements, variables) {
  return {
    type: FETCH_GRAPH,
    payload: { requirements, variables }
  };
}

export function fetchGraphComplete(data, rootPayload) {
  return {
    type: FETCH_GRAPH_COMPLETE,
    payload: data,
    rootPayload
  };
}

export function fetchGraphFailed(response, rootPayload) {
  return {
    type: FETCH_GRAPH_FAILED,
    payload: response,
    rootPayload
  };
}
